.p-pc-nav__items {
  display: flex;
  align-items: center;
  height: inherit;
}

.p-pc-nav__item {
  height: inherit;
  padding: 0 1.25rem;
}

.p-pc-nav__item-link {
  display: flex;
  align-items: center;
  height: inherit;
  position: relative;
  text-shadow: 0 2px 2px #666;
}

.p-pc-nav__item-link::after {
  position: absolute;
  bottom: 2.1875rem;
  left: 0;
  content: "";
  width: 100%;
  height: 0.125rem;
  background-color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: 0.5s;
  box-shadow: 0 2px 2px #666;
}

.p-pc-nav__item-link:hover::after {
  content: "";
  opacity: 0;
  animation-name: fadein-bottom;
  animation-duration: 0.8s;
  animation-timing-function: ease-out;
  animation-fill-mode: forwards;
  background-color: #fff;
  bottom: 0.5625rem;
  visibility: visible;
}

@keyframes fadein-bottom {
  0% {
    opacity: 0;
    transform: translateY(10px);
  }
  100% {
    opacity: 1;
    transform: translateY(-12px);
  }
}
